/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

a {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

.d-none-pc {
  display: none;
}

.footer .d-none-pc {
  display: none;
}

.footer .snslist {
  height: 50px;
  margin-top: 82px;
  margin-bottom: 80px;
  text-align: center;
}

.footer .snslist i, .footer .snslist img {
  vertical-align: bottom !important;
}

.footer .snslist .sns__list {
  width: 50px;
  height: 50px;
  margin-right: 70px;
}

.footer .snslist .sns__list .fa-4x {
  font-size: 50px;
}

.footer .snslist .sns__list .fa-facebook {
  color: #1877f2;
}

.footer .footer__box {
  background-color: #6ac7fa;
  padding-bottom: 48px;
}

.footer .footer__box .footer_inner {
  width: 1080px;
  margin: 0 auto;
}

.footer .footer__box .footer_inner p.white {
  color: #fff;
  text-align: center;
}

.footer .footer__box .footer_inner #footer {
  color: #fff;
  display: flex;
  padding-top: 60px;
}

.footer .footer__box .footer_inner #footer a {
  color: #fff;
  position: relative;
  opacity: 0.7;
  transition: all .3s;
}

.footer .footer__box .footer_inner #footer ul {
  margin-right: 35px;
  list-style: none;
}

.footer .footer__box .footer_inner #footer ul li {
  width: 143px;
  font-size: 13px;
  line-height: 23px;
  margin-bottom: 21px;
}

.footer .footer__box .footer_inner #footer ul li:first-child {
  font-weight: 700;
  opacity: 1;
}

.footer .footer__box .footer_inner #footer .footer__sub .footer__btn {
  width: 150px;
  height: 30px;
  line-height: 30px;
  border: 1px solid #FFF;
  color: #FFF;
  text-align: center;
  font-size: 13px;
  margin-bottom: 20px;
}

.main .main_top {
  position: relative;
  margin: 0 auto 100px;
  width: 1366px;
}

.main .main_top .top--img ul {
  list-style: none;
}

.main .main_top #slide {
  width: 100%;
  height: 600px;
  margin: 0 auto;
  position: relative;
}

.main .main_top #slide ul {
  list-style: none;
}

.main .main_top #slide ul li img {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.main .main_top .viewer {
  display: none !important;
}

.news {
  width: 1080px;
  margin: 0 auto;
  text-align: center;
}

.news h1.important {
  font: normal bold 45px/77px YuGothic;
}

.news .newslist {
  margin-bottom: 160px;
  border: 1px #333 solid;
  width: 1080px;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.services {
  background-color: #fffce8;
  margin-top: 100px;
  margin-bottom: 100px;
}

.services a {
  text-decoration: none;
}

.services .serviceslist {
  margin: 0 auto;
  width: 1080px;
}

.services .serviceslist h1 {
  font: normal bold 45px/77px YuGothic;
  padding-top: 120px;
  padding-bottom: 40px;
  text-align: center;
}

.services .serviceslist .shopping {
  flex-direction: row-reverse;
}

.services .serviceslist .ballet, .services .serviceslist .shopping, .services .serviceslist .events {
  margin: 60px 0;
  display: flex;
  width: 1080px;
  justify-content: space-between;
  align-content: center;
}

.services .serviceslist .ballet .btn__service, .services .serviceslist .shopping .btn__service, .services .serviceslist .events .btn__service {
  background-color: #FFF;
  border: 2px solid #222;
  margin-top: 85px;
  width: 300px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  border-radius: 25px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  transition: all 0.3s ease;
}

.services .serviceslist .ballet .btn__service .span__blue, .services .serviceslist .shopping .btn__service .span__blue, .services .serviceslist .events .btn__service .span__blue {
  color: #6ac7fa;
  display: inline-block;
  margin-right: -80px;
  padding-left: 50px;
}

.services .serviceslist .ballet .service__img__top, .services .serviceslist .shopping .service__img__top, .services .serviceslist .events .service__img__top {
  width: 500px;
  height: 320px;
}

.services .serviceslist .ballet .service__img__top img, .services .serviceslist .shopping .service__img__top img, .services .serviceslist .events .service__img__top img {
  width: 100%;
  object-fit: cover;
  height: 100%;
}

.services .serviceslist .ballet .service__text h2.h2_yuminscho, .services .serviceslist .shopping .service__text h2.h2_yuminscho, .services .serviceslist .events .service__text h2.h2_yuminscho {
  font: normal bold 40px/60px YuGothic;
  margin-bottom: 40px;
  margin-top: 40px;
}

.services .serviceslist .ballet .service__text p, .services .serviceslist .shopping .service__text p, .services .serviceslist .events .service__text p {
  width: 515px;
  line-height: 28px;
}

.services .serviceslist .center {
  display: flex;
  justify-content: center;
  margin: 120px auto 0;
  padding-bottom: 120px;
}

.services .serviceslist .center a .eventlists__btn {
  background-color: #FFA53C;
  color: #ffffff;
  font-size: 25px;
  font-weight: bold;
  width: 800px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.services .serviceslist .center a .eventlists__btn::before {
  content: "";
  width: 20px;
  height: 20px;
  background: linear-gradient(135deg, #ffcf4b 0%, #ffcf4b 50%, #fffce8 50%);
  display: inline-block;
  position: absolute;
  bottom: -1px;
  right: -1px;
}

.top-container h1 {
  text-align: center;
}

.top-container h1.important {
  font: normal bold 45px/77px YuGothic;
  margin-bottom: 52px;
}

.top-container .catchcopy__p {
  width: 800px;
  text-align: initial;
  margin: 50px auto 0;
  font-size: 20px;
  line-height: 38px;
}

.top-container .center {
  display: flex;
  justify-content: center;
}

.top-container .center .btn__about {
  margin-top: 70px;
  background-color: #555;
  width: 300px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  box-sizing: border-box;
  border: 2px solid #555;
  border-radius: 25px;
  cursor: pointer;
  text-align: center;
  color: #FFF !important;
  text-decoration: none;
  align-self: center;
  transition: all 0.3 ease;
}

.top-container .center .btn__about .span__blue {
  color: #6ac7fa;
  display: inline-block;
  margin-right: -80px;
  padding-left: 50px;
}

.btn__contact {
  background-color: #ffcf4b;
  border: 3px solid #333;
  border-bottom: 5px solid #333;
  border-radius: 10px;
  display: inline-block;
  width: 800px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  transition: all 0.3s ease;
}

.contact {
  background-color: #555;
  color: #FFF !important;
  width: 150px;
  height: 40px;
  line-height: 40px;
  margin-top: 30px;
  overflow: hidden;
  text-align: center;
  cursor: pointer;
}
